import axios from 'axios'

class ApiProvider {
    constructor(){
        this.url = process.env.VUE_APP_API_URL ? process.env.VUE_APP_API_URL : ''
    }

    async register({ email, password, role }){
        try {
            const query = '/api/user/register'
            const { data: user } = await axios.post(this.url + query, {email, password, role})
            console.log('register log: ', user)
            return user   
        } catch (err) {
            console.log(err)
            window.alert.error(err.response.data.error)
        }
    }

    async login({ email, password }){
        try {
            const query = '/api/user/login'
            const { data: { token } } = await axios.post(this.url + query, {email, password})
            console.log('login log: ', user)
            return token
        } catch (err) {
            window.alert.error(`${err.name} - ${err.message} - ${err.status}`)
        }
    }

    async getAllUsersPaginated({ page = 1, limit = 10 }){
        try {
            const query = '/api/user/?page=' + page + '&lmit=' + limit
            const users = await axios.get(this.url + query)
            console.log('getAllUsersPaginated log: ', users)
            return users
        } catch (err) {
            window.alert.error(err.message)
        }
    }

    async authenticateme({ token }){
        try {
            const query = '/api/user/me'
            const user = await axios.get(this.url + query, {
                headers: {
                    Authorization: `Bearer ${token}`
                }
            })
            console.log('authenticateme log: ', user)
            return user
        } catch (err) {
            window.alert.error(err.message)
        }
    }
}

export default new ApiProvider()