<template>
  <v-form v-model="formValue">
    <input-email v-model="email"/>
    <input-password-and-repeat-password v-model="password"/>
    <v-btn 
        block 
        color="primary"
        @click="register"
        :loading="loading"
        :disabled="disabledRegister">Registrarse</v-btn>
  </v-form>
</template>

<script>
import InputPasswordAndRepeatPassword from './components/InputPasswordAndRepeatPassword.vue'
import InputEmail from './components/InputEmail.vue'

import apiProvider from '@/provider/apiProvider'
import { mapMutations } from 'vuex'

export default {
    name: 'register-form',
    components: {
        InputEmail,
        InputPasswordAndRepeatPassword
    },
    data(){
        return{
            disableInputs: false,
            formValue: false,
            loading: false,
            email: '',
            password: '',
            user: {}
        }
    },
    computed: {
        disabledRegister(){
            return !this.formValue
        }
    },
    methods: {
        ...mapMutations(['setHormiguerToken']),
        async register(){
            this.disableInputs = true
            this.loading = true
            try {
                this.user = await apiProvider.register({
                    email: this.email, 
                    password: this.password,
                    role: 'hormiguer'
                })
                window.alert.success('¡Felicitaciones! Tu registro se ha completado con éxito.')
                await this.login()
            } catch (err) {
                window.alert.error("Lo siento, no pudimos completar tu registro en este momento. Por favor, inténtalo de nuevo más tarde o ponte en contacto con nuestro equipo de soporte si el problema persiste. Gracias.")
                this.$router.push('/')
            }finally {
                this.loading = false
                this.disableInputs = false
            }
        },
        async login(){
            try {
                this.token = await apiProvider.login({
                    email: this.email,
                    password: this.password
                })
                this.setHormiguerToken(token)
                this.$router.push('/hormiguer')
            }catch(error){
                window.alert.error("Tu registro ha sido completado con éxito, pero no pudimos procesar tu solicitud de inicio de sesión en este momento. Por favor, inténtalo de nuevo más tarde o ponte en contacto con nuestro equipo de soporte si el problema persiste. Gracias por tu comprensión.")
                this.$router.push('/')
            }
        }
    }
}
</script>

