<template>
  <div id="map" style="width:100%; height: 100%;"></div>
</template>

<script>
/* global google */

export default {
    name: 'travel-map',
    props: {
        locations: {
            type: Array,
            required: true
        }
    },
    mounted() {
    const map = new google.maps.Map(document.getElementById("map"), {
      center: { lat: this.locations[0].lat, lng: this.locations[0].lng },
      zoom: 10,
      disableDefaultUI: true, // Desactiva los controles por defecto del mapa
      draggable: false, // Desactiva la función de arrastrar el mapa
      scrollwheel: false, // Desactiva la función de hacer zoom con la rueda del mouse
      zoomControl: false, // Desactiva los controles de zoom
      mapTypeControl: false, // Desactiva los controles de cambiar el tipo de mapa
      scaleControl: false, // Desactiva la escala en el mapa
      streetViewControl: false, // Desactiva los controles de Street View
      rotateControl: false, // Desactiva los controles de rotación del mapa
      styles: [  // Elimina los nombres de las carreteras y la información de transporte público  
        {    
          featureType: "transit",    
          elementType: "labels.icon",    
          stylers: [{ visibility: "off" }]
        },
        {
          featureType: "road.highway",
          elementType: "labels",
          stylers: [{ visibility: "off" }]
        },
        {
          featureType: "road.arterial",
          elementType: "labels",
          stylers: [{ visibility: "off" }]
        },
        {
          featureType: "road.local",
          elementType: "labels",
          stylers: [{ visibility: "off" }]
        },

        // Estilo de tierra y agua más brillante para resaltar áreas naturales y marítimas
        {
          featureType: "water",
          elementType: "geometry.fill",
          stylers: [{ color: "#c6d7ee" }]
        },
        {
          featureType: "landscape.natural",
          elementType: "geometry.fill",
          stylers: [{ color: "#f5f5f2" }]
        },
        
        // Estilo de etiquetas más oscuro para mejorar la legibilidad
        {
          featureType: "poi",
          elementType: "labels.text.fill",
          stylers: [{ color: "#484848" }]
        },
        {
          featureType: "poi.park",
          elementType: "labels.text.fill",
          stylers: [{ color: "#6b9a76" }]
        },

        // Estilo de carretera más suave para reducir la distracción
        {
          featureType: "road.highway",
          elementType: "geometry.stroke",
          stylers: [{ color: "#f5f5f5" }]
        },
        {
          featureType: "road.arterial",
          elementType: "geometry.stroke",
          stylers: [{ color: "#f5f5f5" }]
        },
        {
          featureType: "road.local",
          elementType: "geometry.stroke",
          stylers: [{ color: "#f5f5f5" }]
        },

        // Etiquetas de puntos de interés más pequeñas para reducir la distracción
        {
          featureType: "poi",
          elementType: "labels.icon",
          stylers: [{ visibility: "off" }]
        },

        // Resalta las líneas de transporte público
        {
          featureType: "transit.line",
          elementType: "geometry.stroke",
          stylers: [{ color: "#c6d7ee" }]
        },

        // Sacar localidades
        {
          featureType: "administrative",
          elementType: "labels.text.fill",
          stylers: [{ visibility: "off" }]
        }
      ]
    });
    
    new google.maps.Marker({
        position: { lat: this.locations[0].lat, lng: this.locations[0].lng },
        map,
        title: "Punto 1",
        icon: {
            url: "/package-marker.png",
            size: new google.maps.Size(32, 32),
            origin: new google.maps.Point(0, 0),
            anchor: new google.maps.Point(16, 16)
        }
    });

    new google.maps.Marker({
        position: { lat: this.locations[1].lat, lng: this.locations[1].lng },
        map,
        title: "Punto 2",
        icon: {
            url: "/destination-marker.png",
            size: new google.maps.Size(32, 32),
            origin: new google.maps.Point(0, 0),
            anchor: new google.maps.Point(16, 32)
        }   
    });

    const directionsService = new google.maps.DirectionsService();
    const directionsRenderer = new google.maps.DirectionsRenderer({
      map: map,
      suppressMarkers: true,
      polylineOptions: {
        strokeColor: "#6CB33F",
        strokeOpacity: 0.8,
        strokeWeight: 6
      },
      suppressInfoWindows: true
    });

    const start = new google.maps.LatLng(this.locations[0].lat, this.locations[0].lng);
    const end = new google.maps.LatLng(this.locations[1].lat, this.locations[1].lng);

    const request = {
      origin: start,
      destination: end,
      travelMode: google.maps.TravelMode.DRIVING
    };

    
    directionsService.route(request, (result, status) => {
      if (status == google.maps.DirectionsStatus.OK) {
        directionsRenderer.setDirections(result);
      }
    });
  }
}
</script>

