<template>
  <v-form v-model="formValue">
    <v-text-field 
      label="Correo electronico" 
      append-icon="email" 
      :rules="emailRules"
      v-model="email"
      hint="Ingrese su correo electrónico a continuación"
    />
    <v-text-field 
      label="Contraseña"
      append-icon="lock"
      :rules="passwordRules"
      v-model="password"
      hint="Ingrese su contraseña a continuación"
    />
    <v-btn
      block
      color="primary"
      :loading="loading"
      @click="login()"
    >Iniciar Sesión</v-btn>
  </v-form>
</template>

<script>
  export default {
    name: 'login-form',
    data() {
      return {
        emailRules: [
          value => !!value || 'El correo electrónico es requerido', 
          value => /.+@.+\..+/.test(value) || 'El correo electrónico debe ser válido'
        ],
        passwordRules: [
          value => !!value || 'La contraseña es requerida', 
          value => value.length >= 8 || 'La contraseña debe tener al menos 8 caracteres'
        ],
        loading: false,
        email: '',
        password: ''
      }
    },
    methods: {
      login(){
        this.loading = true
        setTimeout(this.loading = false, 1000)
      }
    }
  }
</script>

