import Vue from 'vue'
import VueRouter from 'vue-router'
import BaseRouter from '../modules/Base/router'
import ClientsRoutes from '@/modules/clients/router'
import HormigersRoutes from '@/modules/hormiguers/router'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  routes: []
})

BaseRouter.forEach(route => {
  router.addRoute(route)
})

ClientsRoutes.forEach(route => {
  router.addRoute(route)
})

HormigersRoutes.forEach(route => {
  router.addRoute(route)
})

export default router
