import { v4 as uuidv4 } from 'uuid'

export const AlertModule = {
    state: {
        alerts: []
    },
    mutations: {
        newAlert(state, {message, type}){
            const id = uuidv4()
            state.alerts.push({
                message: message,
                type: type || 'error',
                id
            })
            setTimeout(()=>{
                const index = state.alerts.findIndex(alert => alert.id === id)

                if(index !==-1){
                    state.alerts.splice(index, 1)
                }
            }, 10000)
        },
    },
    actions: {
        showErrorMessage(context, message) {
            context.commit("newAlert", { message, type: "error" });
        },
        showInfoMessage(context, message) {
            context.commit("newAlert", { message, type: "info" });
        },
        showWarningMessage(context, message) {
            context.commit("newAlert", { message, type: "warning" });
        },
        showSuccessMessage(context, message) {
            context.commit("newAlert", { message, type: "success" });
        }
    }
}