<template>
    <v-row>
        <v-col v-for="(item, index) in items" :key="index" :cols="12">
            <v-row align-items="center" justify-content="center">
                <v-col :cols="5" align-self="end">
                    <input-address label="Lugar" v-model="item.place" @address-selected="value => updateItem(value, index)"/>
                </v-col>
                <v-col :cols="5">
                    <input-time v-model="item.time" />
                </v-col>
                <v-col :cols="2" align-self="center">
                    <v-btn color="error" icon small @click="deleteItem(index)"><v-icon>mdi-delete</v-icon></v-btn>
                </v-col>
            </v-row>
        </v-col>
        <v-col :cols="2">
            <v-btn color="primary" icon small @click="addItem"><v-icon>mdi-plus</v-icon></v-btn>
        </v-col>
    </v-row>
</template>
  
<script>
import InputAddress from './InputAddress.vue';
    import InputTime from './InputTime.vue'

  export default {
    name: 'input-places',
    components: {
        InputTime,
        InputAddress
    },
    props: {
      value: {
        type: Array,
        required: true
      }
    },
    computed: {
      items(){
        return [...this.value]
      }
    },
    methods: {
      updateItem(value, index) {
        console.log(value.description)
        let items = this.items
        items.splice(index, 1, { place: value.description, time: this.items[index].time })
        console.log(items)
        this.$emit('input', items);
      },
      deleteItem(index) {
        this.$emit('input', this.items.length > 1 ? this.items.splice(index, 1) : [])
      },
      addItem() {
        this.$emit('input', [...this.items, {place: '', time: ''}])
      }
    }
  }
</script>