<template>
    <div>
        <v-text-field
        :label="label"
        :value="formattedTime"
        @click="dialog = true"
        readonly
        ></v-text-field>
        <v-dialog v-model="dialog" persistent width="370">
        <v-card>
            <v-card-title>
            {{ title }}
            <v-spacer></v-spacer>
            <v-btn icon @click="dialog = false">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            </v-card-title>
            <v-card-text>
            <v-time-picker
                v-model="time"
                color="primary"
                format="24hr"
                :no-title="noTitle"
            ></v-time-picker>
            </v-card-text>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" text @click="dialog = false">OK</v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>
    </div>
</template>
  
<script>
  export default {
    props: {
      label: {
        type: String,
        default: "Time"
      },
      value: {
        type: String,
        default: ""
      },
      landscape: {
        type: Boolean,
        default: false
      },
      noTitle: {
        type: Boolean,
        default: false
      },
      interval: {
        type: Number,
        default: 30
      },
      title: {
        type: String,
        default: "Seleccione el horario"
      }
    },
    data() {
      return {
        dialog: false,
        time: this.value
      };
    },
    computed: {
      formattedTime() {
        return this.time
      }
    },
    watch: {
      value(newValue) {
        this.time = newValue;
      },
      time(newTime) {
        this.$emit("input", newTime);
      }
    }
  };
</script>
  