<template>
    <v-text-field 
        hint="Ingrese su numero de telefono" 
        append-icon="phone"
        v-model="phone" 
        :label="label" 
        :rules="rules"
    />
</template>
  
<script>
    export default {
        props: {
            value: String,
            label: {
                type: String,
                default: 'Teléfono'
            }
        },
        data() {
            return {
                rules: [
                    (v) => !!v || 'El teléfono es requerido',
                    (v) => /^\d{10}$/.test(v) || 'El teléfono debe ser un número de 10 dígitos'
                ]
            }
        },
        computed: {
            phone: {
                get(){
                    return this.value
                },
                set(value){
                    this.$emit('input', value)
                }
            }
        }
    }
</script>
  