<template>
  <div>
    <v-text-field
      :label="passwordLabel"
      :type="showPassword ? 'text' : 'password'"
      :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
      @click:append="showPassword = !showPassword"
      v-model="password"
      :rules="passwordRules"
    ></v-text-field>
    <v-text-field
      :label="passwordRepeatLabel"
      :type="showPasswordRepeat ? 'text' : 'password'"
      :append-icon="showPasswordRepeat ? 'mdi-eye' : 'mdi-eye-off'"
      @click:append="showPasswordRepeat = !showPasswordRepeat"
      v-model="passwordRepeat"
      :rules="[...passwordRules, passwordMatch]"
    ></v-text-field>
  </div>
</template>

<script>
export default {
  name: 'password-input',
  props: {
    value: String,
    label: String,
    repeatLabel: String,
    rules: {
      type: Array,
      default: () => [
        value => !!value || 'La contraseña es requerida',
        value => value.length >= 8 || 'La contraseña debe tener al menos 8 caracteres',
      ]
    }
  },
  data() {
    return {
      password: '',
      passwordRepeat: '',
      showPassword: false,
      showPasswordRepeat: false
    };
  },
  computed: {
    passwordLabel() {
      return this.label || 'Contraseña';
    },
    passwordRepeatLabel() {
      return this.repeatLabel || 'Repetir contraseña';
    },
    passwordMatch() {
      return value => value === this.password || 'Las contraseñas no coinciden';
    },
    passwordRules() {
      return this.rules;
    }
  },
  watch: {
    password(value) {
      this.$emit('input', value);
    },
    value(newValue) {
      this.password = newValue;
    }
  }
};
</script>
