<template>
  <!-- @change="onAddressSelected" -->
  <v-autocomplete
    append-icon="location_on"
    :clearable="false"
    :label="label"
    :hint="hint"
    :items="addresses"
    :value="address"
    :no-filter="true"
    :menu-props="{ maxHeight: '400' }"
    @input.native="onAddressInput"
    @change="onAddressSelected"
    ref="addressAutocomplete"
    dense
  ></v-autocomplete>
</template>
  
<script>
  
  export default {
    name: 'AddressField',
    props: {
        label: {
            type: String,
            required: true
        },
        hint: {
            type: String, 
            required: false
        }
    },
    data() {
      return {
        address: '',
        addresses: [],
        selectedAddress: ''
      }
    },
    methods: {
      onAddressInput(event) {
        const text = event.target.value
        if (text.length > 3) {
          const service = new google.maps.places.AutocompleteService();
          service.getPlacePredictions({ input: text, componentRestrictions: { country: 'ar' } }, this.processAddresses);
        }
      },
      processAddresses(predictions, status) {
        if (status === google.maps.places.PlacesServiceStatus.OK) {
          this.addresses = predictions.map(prediction => {
            return {
              text: prediction.description,
              value: prediction
            }
          })
        }
      },
      onAddressSelected(value){
        const service = new google.maps.Geocoder()
        service.geocode({placeId: value.place_id}, (results, status) => {
          if(status === google.maps.GeocoderStatus.OK){ 
            this.$emit('address-selected', {
              ...value,
              location: {
                lat: results[0].geometry.location.lat(),
                lng: results[0].geometry.location.lng()
              }
            })
          }else {
            window.alert.error(status)
          }
        })
      }
    }
  }
</script>
  
