<template>
    <v-text-field 
        hint="Ingrese su nombre completo" 
        append-icon="person" 
        v-model="name" 
        :label="label" 
        :rules="rules" 
    />
  </template>
  
  <script>
  export default {
    props: {
      value: String,
      label: {
        type: String,
        default: 'Nombre completo'
      }
    },
    data() {
      return {
        rules: [
          (v) => !!v || 'El nombre completo es requerido',
          (v) => (v && v.length >= 2) || 'El nombre completo debe tener al menos 2 caracteres',
          (v) => (v && v.length <= 50) || 'El nombre completo no debe tener más de 50 caracteres'
        ]
      }
    },
    computed: {
        name: {
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    }
  }
  </script>
  