import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import store from './store'
import router from './router'
import './registerServiceWorker'

Vue.config.productionTip = false

//COMPORTAMIENTO DE ALERTAS

const Alert = {
  error: (message) => {
    store.dispatch('showErrorMessage', message)
  },
  info: (message) => {
    store.dispatch('showInfoMessage', message)
  },
  success: (message) => {
    store.dispatch('showSuccessMessage', message)
  },
  warning: (message) => {
    store.dispatch('showWarningMessage', message)
  }
}

window.alert = Alert

/*
  Ejemplo de uso:
  
  const {info, success, warning, error} = window.alert
  info('info message: ' + i)
  success('success message: ' + i)
  warning('warning message: ' + i)
  error('error message: ', + i)
*/

new Vue({
  store,
  router,
  vuetify,
  render: h => h(App)
}).$mount('#app')
