<template>
    <v-text-field
        v-model="email"
        append-icon="email"
        label="Correo electrónico"
        hint="Ingrese su correo electrónico para registrarse"
        :rules="[rules.required, rules.email]"
    ></v-text-field>
</template>
  
    <script>
    export default {
        name: 'input-email',
        props: {
            value: {
                type: String,
                required: true
            }
        },
        computed: {
            email: {
                get(){
                    return this.value
                },
                set(value){
                    this.$emit('input', value)
                }
            }
        },
        data() {
            return {
            rules: {
                required: value => !!value || 'Este campo es requerido.',
                email: value => /.+@.+\..+/.test(value) || 'Ingrese un correo electrónico válido.',
            },
            }
        },
    };
    </script>
  