<template>
  <v-dialog v-model="dialog">
    <v-card>
        <v-card-title>Hormiguer dialog</v-card-title>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
    name: 'hormiguer-dialog',
    props:{
        value:{
            type: Boolean,
            required: true
        }
    },
    computed:{
        dialog:{
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    }
}
</script>

