<template>
  <v-row>
    <v-col cols="12">
        <v-form>
            <addres-field 
                label="¿En dónde quiere que busquemos su paquete?" 
                hint="" 
                @address-selected="address => originSelected(address)"/>
            <addres-field 
                label="¿A Dónde quiere que llevemos su paquete?" 
                hint="" 
                @address-selected="address => destinationSelected(address)"/>
        </v-form>
        <v-card v-if="allSelected" class="mt-6" width="100%" height="500">
            <travel-map :locations="locations"/>
        </v-card>
        <v-btn
            class="mt-4" 
            block
            color="primary"
            :loading="searching"
            @click="search"
            v-if="allSelected">Buscar hormiguer</v-btn>
    </v-col>
  </v-row>
</template>

<script>
import TravelMap from './components/TravelMap.vue'
import AddresField from './components/AddresField.vue'
export default {
    name: 'search-hormiguer-form',
    components: {
        AddresField,
        TravelMap
    },
    props:{
        value:{
            type: Object,
            required: true
        }
    },
    data(){
        return{
            searching: false,
            locations: [],
            origin: '',
            destination: '',
            originIsSelected: false,
            destinationIsSelected: false,
        }
    },
    computed:{
        hormiguer:{
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        },
        allSelected(){
            return this.originIsSelected && this.destinationIsSelected
        }
    },
    methods: {
        originSelected(value){
            this.originIsSelected = false
            setTimeout(()=>{
                this.locations[0] = value.location
                this.originIsSelected = true
            }, 0)
        },
        destinationSelected(value){
            this.destinationIsSelected = false
            setTimeout(()=>{
                this.locations[1] = value.location
                this.destinationIsSelected = true
            }, 1)
        },
        search(){
            this.searching = true
            setTimeout(()=>{
                this.searching = false
            }, 3000)
        }
    }
}
</script>

